.location-lists {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 30px 0;

    &__container {
        max-width: 1920px;
        width: 100%;

    }

    &__ul {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 10px;
    }
}